  width: 77%;
  background: linear-gradient(293.88deg, #53D750 15.34%, #8DF33C 85.28%);
}
.reports-view__percent {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  font-weight: 600;
}
@media screen and (max-width: 768px) {
  .reports-view__item {
    flex-basis: 100%;
  }
}

.charts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  flex-basis: 49%;
  row-gap: 20px;
}
.charts__item {
  flex-basis: 32%;
  width: 100%;
  background: #FFFFFF;
  border-radius: 16px;
  padding: 18px 14px;
}
.charts__text {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  margin-bottom: 24px;
  font-weight: 400;
}
.charts__title {
  font-family: "Inter", sans-serif;
  font-size: 24px;
  margin-bottom: 10px;
  font-weight: 600;
}
.charts__title b {
  font-size: 14px;
  color: rgba(36, 34, 32, 0.2);
  font-weight: 400;
}
.charts__chart {
  width: 100%;
}
.charts__chart img {
  width: 100%;
}
@media screen and (max-width: 1240px) {
  .charts {
    flex-basis: 100%;
  }
  .charts__item {
    flex-basis: 15%;
  }
}
@media screen and (max-width: 1000px) {
  .charts__item {
    flex-basis: 32%;
  }
}
@media screen and (max-width: 520px) {
  .charts__item {
    flex-basis: 48%;
  }
}
@media screen and (max-width: 350px) {
  .charts__item {
    flex-basis: 100%;
  }
}

.programs {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  gap: 10px;
}
.programs__item {
  max-width: 400px;
  width: 100%;
  padding: 18px 14px;
  background: #FFFFFF;
  border-radius: 16px;
}
.programs__top {
  width: 100%;
  display: flex;
  gap: 10px;
  align-items: center;
  margin-bottom: 20px;
}
.programs__img {
  max-width: 70px;
  max-height: 70px;
}
.programs__img img {
  width: 100%;
  height: 100%;
